
*{
box-sizing: border-box;
padding: 0;
margin: 0;
}
.container{
  text-align: center;
}
form{
  width: 100%;
}
input.addInput{
  width: calc(100% - 70px);
  padding: 5px;

}
button.addBtn{
  width: 70px;
  padding: 7px;
  text-transform: capitalize;
  border: none;
  border:2px solid #fff;
  color: #fff;
}

ul{
list-style: none;
padding-left: 0;
}

.formUpdate{
width: 100% !important;
}
.formUpdate input{
  width: calc(100% - 120px) !important;
  padding: 6px;
  text-transform: capitalize;
  /* border:2px solid #fff; */
}
.formUpdate button{
  width:120px !important;
  padding: 6px;
  border:2px solid #fff;
}
.editForm{
  display: flex;
}

.editForm span{
  width: calc(100% - 200px);
  text-align: left;
  display: inline-block;
  overflow: hidden;
  text-transform: capitalize;
  
}
.editForm button{
  display: inline-block;
  width: 120px;
  padding: 6px;
  border:2px solid #fff;
  text-transform: capitalize;
  
}
button.editBtn:hover{
background-color:aquamarine
}
button.red{
  background-color: #ff0000;
  color:#fff
}